"use client";

import { useState } from "react";
import Link from "next/link";

const topics = [
  {
    id: "database",
    title: "Database Management",
    chapters: [
      "Introduction to Databases",
      "Data Models",
      "SQL Basics",
      "Advanced Queries",
      "Normalization",
      "Exam Practice",
    ],
  },
  {
    id: "programming",
    title: "Programming Fundamentals",
    chapters: [
      "Introduction to Programming",
      "Variables & Data Types",
      "Control Structures",
      "Functions",
      "Arrays & Strings",
      "Problem Solving Practice",
    ],
  },
  {
    id: "networks",
    title: "Data Communication & Networking",
    chapters: [
      "Basics of Networking",
      "Transmission Media",
      "Network Devices",
      "OSI Model",
      "TCP/IP",
      "Network Security Basics",
    ],
  },
];

export default function Database() {
  const [activeTopic, setActiveTopic] = useState(topics[0]);

  return (
    <section className="py-12">
   

      <div className="grid lg:grid-cols-12 gap-8">
        {/* LEFT – MODERN TOPIC LIST */}
        <div className="lg:col-span-4 space-y-3">
          {topics.map((topic, index) => {
            const isActive = activeTopic.id === topic.id;

            return (
              <button
                key={topic.id}
                onClick={() => setActiveTopic(topic)}
                className={`w-full text-left rounded-2xl px-5 py-4 border transition
                  ${
                    isActive
                      ? "bg-black text-white border-black"
                      : "bg-white hover:bg-gray-50"
                  }`}
              >
                <div className="flex items-center gap-4">
                  {/* Topic Number */}
                  <span
                    className={`text-2xl font-bold ${
                      isActive ? "text-white" : "text-gray-300"
                    }`}
                  >
                    {String(index + 1).padStart(2, "0")}
                  </span>

                  {/* Topic Title */}
                  <h3 className="font-semibold text-base">
                    {topic.title}
                  </h3>
                </div>
              </button>
            );
          })}
        </div>

        {/* RIGHT – CHAPTER PANEL */}
        <div className="lg:col-span-8">
          <div className="bg-white rounded-3xl border shadow-sm p-8">
            <h2 className="text-xl font-semibold mb-6">
              {activeTopic.title}
            </h2>

            <ul className="space-y-3">
              {activeTopic.chapters.map((chapter, index) => (
                <li key={chapter}>
                  <Link
                    href={`/study-materials/al-ict/${activeTopic.id}/chapter-${index + 1}`}
                    className="flex items-center justify-between rounded-xl border px-5 py-4 hover:bg-gray-50"
                  >
                    <div>
                      <p className="text-xs text-gray-400">
                        Chapter {index + 1}
                      </p>
                      <p className="font-medium">{chapter}</p>
                    </div>
                    <span className="text-gray-400 text-lg">→</span>
                  </Link>
                </li>
              ))}
            </ul>
          </div>
        </div>
      </div>
    </section>
  );
}
